<template>
  <div>User ID: {{ $route.params.userId  }}, Name: {{ named }}</div>
</template>

<script>
export default {
    name: 'User',
    computed: {
      userId() { return this.$route.params.userId },
      named() { return this.$route.query.name }
    },
    methods: {
      linkToUser() {
        // 跳转到 User 页面的方法，
        this.$router.push({
          path: '/user/' + 2, 
          query: {
            name: 'VcrTing'
          }
        })
      },
      goPage() {
        this.$router.push('/topbar')
        // this.$router.replace('/topbar')
      }
    }
}
</script>

<style>

</style>


